












import { Component, Vue } from 'vue-property-decorator';
import FloorCard from '@/components/FloorCard.vue';
import Floor from '@/models/Floor';

@Component({
  components: {
    FloorCard,
  },
})
export default class Dungeons extends Vue {
  get floors(): Floor[] {
    return this.$store.state.floors[0];
  }
}
